import React, {useState} from 'react'
import './Content.scss'
import List from '../List/List';
import {arrayFilter} from '../../helper/arrayFilter';

const Content = ({data, getApiDataTC}) => {

  const [selected, newSelected] = useState([])
  const [history, setHistory] = useState([[], null, null, null, null, null, null, null, null, null])
  const [count, setCount] = useState(0)

  const getPickOptions = (e) => {
    const value = e.target.value
    if (selected.indexOf(value) === -1) {
      newSelected(prevState => [...prevState, value]) // добавить новый элемент в конец массива
      setCount(count + 1)
    } else {
      newSelected(prevState => [...prevState.filter(item => item !== value)]) // удалить элемент если он уже есть в массиве
      setCount(count + 1)
    }
    arrPush()
  }
  function arrPush () {
    if (history.indexOf(null) !== -1) {
      history[history.indexOf(null)] = selected // если в initialState все еще есть null первый попавшийся делаем selected
      setHistory(prevState => [...prevState])
    } else {
      history.shift() // удаляем первый элемент при переполнении
      setHistory(prevState => [...prevState, selected]) // добавляем последний элемент при переполнении
      setCount(10)
    }
  }
  const prevState = () => {
    setCount(count - 1)
    newSelected([history[count]])
    console.log('prevState count: ', count)
  }
  const nextState = () => {
    setCount(count + 1)
    newSelected([history[count]])
    console.log('nextState count: ', count)
  }
  const resetState = () => {
    newSelected([])
    setCount(0)
    setHistory([[], null, null, null, null, null, null, null, null, null])
    console.log('Я все сбросилхаха!')
  }


  return <div className='contentWrapper'>
    <h1>Content with props</h1>
    <div>
      <button onClick={() => prevState()} disabled={count === 0}>Назад</button>
      <button onClick={() => getApiDataTC()}>Загрузить</button>
      <button onClick={() => nextState()} disabled={count === 10}>Вперед</button>
      <button onClick={() => resetState()}>Сброс</button>
    </div>
    <div>
      {
        data.testArr
          ? arrayFilter(data.testArr).map(i => <select multiple={true}
                                                       id={'select'}
                                                       key={i}
                                                       onChange={getPickOptions}
                                                       className='selectWrapper'
          >{i.map(u => <option key={u}
                               value={u}
          >{u}</option>)}</select>)
          : null
      }
    </div>
    <List history={history[count]}/>
    <span>{count} / 10</span>
  </div>
}

export default Content